import lodash from 'lodash';
import { FC } from 'react';
import { InputErrorStyled } from './style';
import { AtomInputTypes } from './types';

const AtomInputTextError: FC<AtomInputTypes> = (props) => {
  const { formik, id, error } = props;
  return formik ? (
    ((lodash.get(formik?.values, id) !== `` ||
      lodash.get(formik?.touched, id)) &&
      lodash.get(formik?.errors, id)) ||
    error ? (
      <InputErrorStyled {...props}>
        {lodash.get(formik?.errors, id)} {error ?? ''}
      </InputErrorStyled>
    ) : null
  ) : (
    <></>
  );
};

export default AtomInputTextError;
